import React from 'react'
import styled from 'styled-components';
import DescriptionText from '../globalComponents/descriptionText';
import ChapterTitleDesktop from '../globalComponents/chapterTitleDesktop';

import { connect } from 'react-redux';

interface HowItWorksProps{
    WindowWidth:number
}
class HowItWorks extends React.Component<HowItWorksProps,{}>{
    render(){
        return(
            <HowItWorksBox>

                <ChapterTitleDesktop
                title='Jak dziaa laser frakcyjny co2'
                />
               <DescriptionText
                descriptionText='Od zabiegu z uyciem lasera CO2, rozpoczynaj si czsto inne cykle procedur odmadzajcych.
                Jest to wskazane, ze wzgldu na rodzaj jego oddziaywania na skr.
                W miejscu poddanemu dziaaniu promieni lasera, wzbudzane s wasne zdolnoci regeneracyjne organizmu. Mowa tu o produkcji wkien kolagenowych i elastynowych. Dziki temu, napita i obkurczona skra, nie wymaga stosowania duej iloci wypeniaczy.
                Jeli chcesz zadba o skr, midzy innymi w okolicy przedusznej, laser CO2 jest idealnym rozwizaniem, poniewa nie maj tu zastosowania, wspomniane wczeniej wypeniacze.

                Laser CO2 posiada niezalen i pynn regulacj energii, gstoci pokrycia, wielkoci i ksztatu plamki oraz gbokoci penetracji. Dziki temu moliwe jest indywidualne podejcie terapeutyczne do pacjenta.
                Ma te wbudowany skaner, ktry zapobiega nadmiernym uszkodzeniom skry oraz system  chodzcy, dziaajcy znieczulajco, co pozytywnie wpywa na komfort podczas zabiegu.'
                />
            </HowItWorksBox>
        )
    }
}

const HowItWorksBox = styled.section`
    display:flex;
    flex-direction:column;
    align-items:center;
    width:100%;
`



const mapStateToProps = (state: { WindowWidth:number }) => {
    return {

        WindowWidth:state.WindowWidth
    };
  };

  export default connect(mapStateToProps)(HowItWorks);