import React from 'react'
import styled from 'styled-components';

import ChapterTitleDesktop from '../globalComponents/chapterTitleDesktop';
import ListWithTitle from '../globalComponents/listWithTitle';
import BoxUnderList from '../globalComponents/boxUnderList';
import smartM2 from '../../images/lasotronixImgs/laser3.jpg';

import plusIcon from '../../images/plus.png';
import minusIcon from '../../images/minus.png';
import { connect } from 'react-redux';

interface SmartMainSectionProps{
  Indications:string[],
  ContraIndications:string[],
  WindowWidth:number
}
class SmartMainSection extends React.Component<SmartMainSectionProps,{}>{
    render(){
        return(
            <MainBox>

            <ChapterTitleDesktop
            title='Komu i w czym moe pomc laser smart M?'
          />

            <Indications>Wskazania</Indications>


            <Sign src={plusIcon}/>

            <ListWithTitle
            text='Jeli tylko nie ma wyranych przeciwwskaza do zabiegu EVLT z uyciem lasera, warto rozway skorzystanie z niego. Wskazane jest to szczeglnie, gdy cierpisz na jedn lub kilka spord nastpujcych dolegliwoci: '
            array={[
              'krew cofa si w yle, z powodu nieprawidowej funkcji jej zastawek',
              'twoje ylaki wygldaj nieestetycznie i chcesz to zmieni',
              'czujesz, e twoje nogi s bardzo cikie',
              'bol ci nogi',
              'odczuwasz pulsowanie w nogach',
               'cierpisz z powodu przewlekego zmczenia ng',
               'masz zesp niespokojnych ng',
               'czsto pojawiaj si skurcze nocne ng',
               'ylaki samoistnie krwawi'

            ]}
            />
            <BoxUnderList
              firstLine='Speniajc najwysze standardy, Laser SMART M jest bezpieczny '
              secondLine='zarwno dla lekarza, jak i dla pacjenta'
              />
             <ChapterTitleDesktop
            title='Nie mona zastosowa laseroterapii metod EVLT, jeli: '
          />

            <ContraIndications>Przeciwwskazania</ContraIndications>
            <Sign src={minusIcon}/>

            <ListWithTitle
            text='Mimo, i samo dziaanie lasera jest bardzo bezpieczne, s sytuacje, w ktrych
            jego uycie jest niewskazane,a nawet wrcz zabronione'
            array={[
                ' masz skazy naczyniowe',
                 'cierpisz na zaburzenia krzepliwoci krwi',
                'masz nieuregulowane nadcinienie ttnicze',
                 'pojawiy si stany zapalne',
                 'wdaa si infekcja ropna skry i luzwek',
                 'jeste w ciy',
                 'zdiagnozowano u ciebie chorob nowotworow',
                 'masz cukrzyc w zaawansowanym stadium',
                 'twoje yy s zaronite lub s w nich zakrzepy -  w praktyce, uniemoliwia to skuteczne przeprowadzenie narzdzi wewntrznaczyniowych przez naczynie'
            ]}
            />
            <BoxUnderList
              firstLine='Jak przed kadym zabiegiem, tak i tu,'
              secondLine='ostateczn decyzj podejmuje lekarz'
            />


          </MainBox>

        )
    }
}

const MainBox = styled.main`
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;

  width:100%;

  @media(min-width:1200px){
    background-size: auto 40%;
    background-position: 102% 25%;
  background-image:url("${smartM2}");
  background-repeat: no-repeat;
  }


}

`
const Indications = styled.h3`
  width:280px;
  font-size: 0.8em;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 15px;
    &&+p{
      width: 250px;
      color: #999;
      text-align: center;
      font-size: 0.6em;
      text-transform: uppercase;
    }
    @media(min-width:900px){
      font-size: 1.4em;
      width: 400px;
      &&+p{
        font-size:1.4em;
        width:600px;
      }
  }
`
const ContraIndications = styled.h3`
  text-transform: uppercase;
  color: #000;
  font-size: 0.8em;
  @media(min-width:900px){
    font-size:1.4em
  }


`
const Sign = styled.img`
  height:40px;
  width:40px;
`

const mapStateToProps = (state: { CuteraIndications: string[],CuteraContraIndications:string[],WindowWidth:number }) => {
  return {
      Indications: state.CuteraIndications,
      ContraIndications:state.CuteraContraIndications,
      WindowWidth:state.WindowWidth
  };
};

export default connect(mapStateToProps)(SmartMainSection);