import React, { Component } from 'react'
import ChapterTitleDesktop from '../globalComponents/chapterTitleDesktop';
import ListWithTitle from '../globalComponents/listWithTitle';
import styled from 'styled-components';

export class SmartMAdventages extends Component {
    render() {
        return (
            <AdventagesSection>
                <ChapterTitleDesktop
            title='Zalety zabiegu evlt z uzyciem lasera smart m'
          />
          <ListWithTitle
          text='Jeli wci zastanawiasz si, czy warto skorzysta z tego typu zabiegu, oto jego najwaniejsze korzyci, a zarazem to wszystko, co odrnia go od innych, inwazyjnych zabiegw chirurgicznych: '
            array={[
                'nie jest inwazyjny',
                'nie wymaga hospitalizacji - moe by przeprowadzony ambulatoryjnie, dziki czemu, zaraz po zabiegu, moesz wrci do domu',
                'zabieg wykonywany jest bez koniecznoci stosowania znieczulenia oglnego',
                'dziki precyzji wykonania, nie dochodzi do uszkodze nerww i skry',
                'podczas zabiegu, moesz odczuwa niewielki bl, ale te bardzo moliwe, e nie pojawi si w ogle',
                'zabieg nie pozostawia blizn, zasinie i obrzkw',
                'efekt zabiegu jest dugotrway, co w praktyce oznacza skuteczn eliminacj chorych y w 90%-98%)',
                'po zabiegu, moesz szybko wrci do swoich codziennych zaj'
            ]}
          />
            </AdventagesSection>
        )
    }
}
const AdventagesSection = styled.section`

`
export default SmartMAdventages
