import React from "react";
import styled from "styled-components";
import Button from "../globalComponents/button";

import ChapterTitleDesktop from "../globalComponents/chapterTitleDesktop";
import laserOnRight from "../../images/laserOnRight.png";

import { connect } from "react-redux";

interface IntervetionCourseProps {
  WindowWidth: number;
}
class IntervetionCourse extends React.Component<IntervetionCourseProps, {}> {
  render() {
    return (
      <IntervetionCourseBox>

          <ChapterTitleDesktop title="Przebieg zabiegu" />


        <OrderList>
          <li>
            W zalenoci od rodzaju zmian na skrze, parametry zabiegowe zostaj
            odpowiednio dopasowane
          </li>
          <li>
            Wizka emitowana przez laser Excel V nakierowana zostaje na zmian
            naczyniow na skrze.
          </li>
          <li>
            W wyniku jej dziaania, dochodzi do wybirczej fototermolizy, co w
            praktyce oznacza pochonicie energii lasera tylko przez
            hemoglobin. Dziki temu nie dochodzi do uszkodzenia innych
            fragmentw skry
          </li>
          <li>
            Absorpcja energii powoduje podgrzanie naczynek, a nastpnie
            cakowite zamknicie ciany naczynia. Laser, eleminuje w ten sposb
            uszkodzenie naczy, przywracajc skrze normalny odcie i kolor
          </li>
        </OrderList>


        <IntervetionCourseSummaryBox>
        <ChapterTitleDesktop
          title='chodzenie kontaktowe'
          />
          <IntervetionCourseH4>
            Jeszcze wikszy komfort podczas zabiegu.
          </IntervetionCourseH4>
          <IntervetionCourseParagraph>
            Jak ju wiesz, wizki lasera powoduj podgrzanie naczynek na skrze.
            By zagodzi zwizane z tym odczucia, laser Excel V wyposaony
            zosta w najnowoczeniejszy system chodzenia rwnolegego.
          </IntervetionCourseParagraph>
          <IntervetionCourseParagraph>
            W praktyce oznacza to, e temperatura skry, obniana jest przed, w
            trakcie i po emisji impulsu.
          </IntervetionCourseParagraph>
        </IntervetionCourseSummaryBox>


        <ChapterTitleDesktop
          title='Konsultacja lekarska zrb pierwszy krok'
          />
        <IntervetionCourseSummaryBox>


          <IntervetionCourseParagraph>
            Wiesz ju, czym jest, jak dziaa i w czym moe pomc laser Excel V.
            Nastpnym krokiem jest uzyskanie informacji, czy zmiany, ktre
            chcesz usun, mog by poddane dziaaniu lasera
          </IntervetionCourseParagraph>
          <IntervetionCourseParagraph>
            By to zrobi umw si na konsultacj w naszym Centrum
            Medycznym Inter Med.
          </IntervetionCourseParagraph>
          <IntervetionCourseParagraph>
            Rozmowa z lekarzem, pozwoli te rozwia wszelkie wtpliwoci,
            dotyczce zarwno samego zabiegu, jak i efekw jego dziaania, w
            zetkniciu ze zmianami na twojej skrze.
          </IntervetionCourseParagraph>
        </IntervetionCourseSummaryBox>
        <a href="tel:+48601197666">
          <Button btnText="Zapisz si" />
        </a>
      </IntervetionCourseBox>
    );
  }
}

const IntervetionCourseBox = styled.section`

    width:100%;
    display:flex;
    flex-direction:column;
    align-items:center;
    background-image:url("${laserOnRight}");
    background-size: auto 100%;
    background-position: 50% 40px;
    background-repeat: no-repeat;
    padding-bottom:15px;
    @media(min-width:700px){
        background-position: 100% 40px;
      }


`;

const OrderList = styled.ol`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px 0 35px;
  font-size: 0.8em;
  letter-spacing: 1px;
  width: 70%;
  color:#999;
  li {
    width: 100%;
    margin-bottom: 20px;
  }
  @media(min-width:900px){
    width:50%;
    font-size:1em;
  }
`;


const IntervetionCourseSummaryBox = styled.div`

  text-align: center;
  margin-bottom: 15px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media(min-width:900px){
    text-align:left;
    width:100%;
  }
`;
const IntervetionCourseParagraph = styled.p`
  font-size: 0.8em;
  color:#999;
  width:70%;

  @media(min-width:900px){
    font-size:1em;
    width:54%;
  }
`;

const IntervetionCourseH4 = styled.h4`

  font-size:1em;
  font-weight:normal;
  text-transform:uppercase;

  @media(min-width:900px){
    font-size:1.05em;
  }
`;

const mapStateToProps = (state: { WindowWidth: number }) => {
  return {
    WindowWidth: state.WindowWidth
  };
};

export default connect(mapStateToProps)(IntervetionCourse);
