import React, { Component } from 'react'
import ChapterTitleDesktop from '../globalComponents/chapterTitleDesktop';
import ListWithTitle from '../globalComponents/listWithTitle';
import styled from 'styled-components';
export class OverallInfo extends Component {
    render() {
        return (
            <OverallSection>
                <ChapterTitleDesktop
            title='Chirurgiczny laser smart m'
          />
          <ListWithTitle
          text='Laser SMART M wyrnia si pod wzgldem skutecznoci i wszechstronnoci zastosowania. Uywany jest nie tylko w chirurgii naczyniowej, ale te w: '
            array={[
                'proktologii do leczenia hemoroidw I, II, III stopnia, przetok i torbieli ogoniastych',
                'flebologii, gdzie uywany jest do leczenia owrzodze, stopy cukrzycowej, odleyn i obrzkw',
                'mikrochirurgii',
                'laryngologii',
                'okulistyce',
                'ortopedii',
                'neurochirurgii'
            ]}
          />
            </OverallSection>
        )
    }
}
const OverallSection = styled.section`
    width:100%;
`
export default OverallInfo
