import React from 'react'
import styled from 'styled-components';

import ChapterTitleDesktop from '../globalComponents/chapterTitleDesktop';
import ListWithTitle from '../globalComponents/listWithTitle';
import BoxUnderList from '../globalComponents/boxUnderList';
import laser2 from '../../images/co2imgs/laser2.jpg';

import plusIcon from '../../images/plus.png';
import minusIcon from '../../images/minus.png';
import { connect } from 'react-redux';

interface CoMainSectionProps{
  Indications:string[],
  ContraIndications:string[],
  WindowWidth:number
}
class Co2MainSection extends React.Component<CoMainSectionProps,{}>{
    render(){
        return(
            <MainBox>

            <ChapterTitleDesktop
            title='Komu i w czym moe pomc Laser Ultrafrax Reborn?'
          />

            <Indications>Wskazania</Indications>


            <Sign src={plusIcon}/>

            <ListWithTitle
            text='Zadbana i jdrna skra, nie musi by domen jedynie osb
            w modym wieku.O to,w czym moe Ci pomc zabieg z uyciem lasera co2 ultrafrax reborn:'
            array={[
              'rozstpy',
              'rozszerzone pory',
              'blizny po oparzeniach, zarwno te przerose jak i zanikowe',
              'blizny potrdzikowe',
              'wkniaki mikkie, brodawki ojotokowe, znamiona brodawkujce i zmiany przerostowe na skrze'

            ]}
            />
            <BoxUnderList
              firstLine='Speniajc najwysze standardy, Laser CO2 Ultrafrax jest bezpieczny'
              secondLine='Zarwno dla lekarza, jak i dla pacjenta.'
              />
             <ChapterTitleDesktop
            title='Kiedy nie moesz skorzysta z zabiegu z uyciem lasera co2'
          />

            <ContraIndications>Przeciwwskazania</ContraIndications>
            <Sign src={minusIcon}/>

            <ListWithTitle
            text='Mimo, i samo dziaanie lasera jest bardzo bezpieczne, s sytuacje, w ktrych
            jego uycie jest niewskazane,a nawet wrcz zabronione'
            array={[
              'zaywanie lekw przeciwkrzepliwych',
              'okres ciy i karmienia piersi',
              'aktywna opryszczka',
              'zastosowanie samoopalaczy w okresie ostatnich 2 tygodni',
              'przyjmowanie lekw fotouczulajcych',
              'zaywanie izotretynoiny w cigu ostatniego p roku'
            ]}
            />
            <BoxUnderList
              firstLine='Jak przed kadym zabiegiem, tak i tu,'
              secondLine='ostateczn decyzj podejmuje lekarz'
            />


          </MainBox>

        )
    }
}

const MainBox = styled.main`
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  background-image:url("${laser2}");
  background-size: auto 64%;
    background-position: -30px -46px;
  background-repeat: no-repeat;
  width:100%;
  @media(min-width:600px){
    background-size: auto 77%;
    background-position: 0% -5%;
  }


}

`
const Indications = styled.h3`
  width:280px;
  font-size: 0.8em;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 15px;
    &&+p{
      width: 250px;
      color: #999;
      text-align: center;
      font-size: 0.6em;
      text-transform: uppercase;
    }
    @media(min-width:900px){
      font-size: 1.4em;
      width: 400px;
      &&+p{
        font-size:1.4em;
        width:600px;
      }
  }
`
const ContraIndications = styled.h3`
  text-transform: uppercase;
  color: #000;
  font-size: 0.8em;
  @media(min-width:900px){
    font-size:1.4em
  }


`
const Sign = styled.img`
  height:40px;
  width:40px;
`

const mapStateToProps = (state: { CuteraIndications: string[],CuteraContraIndications:string[],WindowWidth:number }) => {
  return {
      Indications: state.CuteraIndications,
      ContraIndications:state.CuteraContraIndications,
      WindowWidth:state.WindowWidth
  };
};

export default connect(mapStateToProps)(Co2MainSection);