import React from 'react'
import styled from 'styled-components';
import Cutera1 from '../../images/cutera1.png';
import Nowoczesnosc from '../../images/mainImages/nowoczesnosc.png'
import Personel from '../../images/mainImages/personel.png'
import Szpital1dnia from '../../images/mainImages/szpital1dnia.png'
import backgroundLaserImg from '../../images/background_deskt.png';
import DescriptionWithImgRight from './laserPresentationComponents/descriptionWithImgRight';
import DescriptionWithImgLeft from './laserPresentationComponents/descriptionWithImgLeft';
import ChapterTitleDesktop from '../globalComponents/chapterTitleDesktop';

export default class LaserPresentation extends React.Component<{},{}>{
    render(){
        return(
            <LaserPresentationBox>


                <ChapterTitleDesktop title='Dlaczego warto skorzysta?'/>
                <DescriptionWithImgLeft
                imgSrc={Personel}
                boxTitle = 'Personel'
                descriptionHeader='Dowiadczony personel lekarski'
                descriptionText='Zatrudniamy ponad setk lekarzy z rnych specjalnoci medycznych. Wszystkie zabiegi laserowe s wykonywane przez czynnych zawodowo, dobrze przeszkolonych i dowiadczonych lekarzy, a nie przez kosmetyczki czy kosmetologw.'
                />
                <DescriptionWithImgRight
                    imgSrc={Szpital1dnia}
                    boxTitle = 'Jako'
                    descriptionHeader='Szpital jednego dnia'
                    descriptionText='Cz zabiegw laserowych (np. usuwanie ylakw ng) jest prowadzona na sali operacyjnej w szpitalu, ktry stanowi integraln cz placwki. Pozostae zabiegi odbywaj si w warunkach ambulatoryjnych.'
                />
                <DescriptionWithImgLeft
                imgSrc={Nowoczesnosc}
                boxTitle = 'Nowoczesno'
                descriptionHeader='Inwestujemy w nowoczesno'
                descriptionText='W Centrach Medycznych INTER-MED do zabiegw i diagnostyki stosujemy najnowsze technologie. rednio raz w roku dla prowadzenia zabiegw i diagnostyki dokupujemy nowoczesne urzdzenia jak np. laser EXCEL V  Cutera.'
                />
            </LaserPresentationBox>
        )
    }
}

const LaserPresentationBox = styled.section`
    display:flex;
    flex-direction:column;
    align-items:center;
    width:100%;
    background-image:url('${backgroundLaserImg}');
    background-repeat:no-repeat;
    background-size: 100% 82%;
    background-position: -1px -145px;

`
