
const initialState = {
  BurgerNavVisible:false,
  SignInVisible:false,
  WindowWidth:window.innerWidth,
  LaserTypes:[
    'Cutera Excel V',
    'Smart M EVLT',
    'Laser CO2',
    'OPTOSLT M',

  ],
  CuteraIndications:[
    'trdzik rowaty',
     'pajczki',
     'rumie',
     'zmiany naczyniowe w nogach',
     'naczyniaki',
     'znamiona',
     'rozproszone zmiany naczyniowe',
     'rozproszone zmiany barwnikowe, takie jak plamy posoneczne',
     'blizny bdce konsekwencj przebytych urazw lub wykonanych zabiegw chirurgicznych'
   ],
  CuteraContraIndications:[
    'cia','wiea opalenizna',' spoywanie naparu z dziurawca i nagietka - naley je odstawi na 3 tygodnie przed zabiegiem',
 'bielactwo', 'uszczyca','skonno do bliznowcw','nowotwory skry',
 'otwarte rany','okolice tatuau i permanentnego tatuau',
 'przyjmowanie lekw wiatouczulajcych i lekw psychotropowych'
  ]

 }

const reducer = (state = initialState, action: { type: string,width:number })=> {
    switch (action.type) {
      case 'BURGERNAVSHOWHIDE':
        return {
          ...state,
          BurgerNavVisible:!state.BurgerNavVisible
        }
        case 'HIDEMENU':
        return {
          ...state,
          BurgerNavVisible:false
        }
      case 'SIGNINWINDOWSHOWHIDE':
        return {
          ...state,
          SignInVisible : !state.SignInVisible
        }
      case 'CHANGEWINDOWWIDTH':
        return{
          ...state,
          WindowWidth:action.width
        }
      default:
        return state
    }
  }

  export default reducer;